@mixin center($direction: 'all') {
  position: absolute;
  @if $direction == 'all' {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  } @else if $direction == 'h' {
    left: 50%;
    transform: translate(-50%, 0);
  } @else if $direction == 'v' {
    top: 50%;
    transform: translate(0, -50%);
  }
}

@mixin ellipsis() {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
